/* General Styling */
.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  background-color: #1a1a1a;
  /* Professional dark grey background */
  color: #e0e0e0;
  /* Light grey text for readability */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.App-header {
  margin: 0 auto;
  max-width: 90%;
  padding: 20px;
  border-radius: 10px;
  background: linear-gradient(145deg, #2c2c2c, #1a1a1a);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);
}

h1 {
  font-size: 2rem;
  color: #f5f5f5;
  margin-bottom: 20px;
}

/* Search Box */
.search-box {
  margin-bottom: 20px;
}

.search-box input {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #444;
  border-radius: 5px;
  background-color: #2c2c2c;
  color: #e0e0e0;
  margin-right: 10px;
}

.search-box button {
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  background-color: #444;
  color: #e0e0e0;
  cursor: pointer;
}

.search-box button:hover {
  background-color: #555;
}

/* Data Display */
.data-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 20px;
}

.data-card {
  background-color: #2c2c2c;
  border: 1px solid #444;
  border-radius: 5px;
  padding: 15px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 600px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

.data-field {
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
}

.field-key {
  font-weight: bold;
  color: #8fbc8f;
  /* Soft green for keys */
}

.field-value {
  color: #e0e0e0;
  text-align: left;
}

/* Hover Effect */
.data-card:hover {
  background-color: #333;
  transition: background-color 0.3s;
}